.h1{
  font-size: 3.75em;
  line-height: 100%;
}

.h1,
.h3,
.h2{
  font-weight: 800;
  span{
    color: $primary;
  }
}

.h2,
.text40{
  font-size: 2.5em;
  line-height: 100%;
}

.h3{
  font-size: 1.875em;
}

.text30{
  font-size: 1.875em;
}

.text24{
  font-size: 1.5em;
  line-height: 100%;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 100%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 100%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 100%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.lh110{
  line-height: 110%;
}

.lh140{
  line-height: 140%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.gray-text{
  color: $gray;
}

.gradient-text{
  background: linear-gradient(90deg, #1B8F45 0%, #A6CE39 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.green-text{
  color: $primary;
}



@media screen and (max-width: 992px){
  .h1{
    font-size: 2em;
  }

  .h2{
    font-size: 1.75em;
  }

  .h3{
    font-size: 1.25em;
  }

  .text24-tablet{
    font-size: 1.5em;
  }

  .text22-tablet{
    font-size: 1.375em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}
