.footer{
  margin-top: -7.125em;
  padding: 6.875em 0 5em;
  background-color: $black;
  color: #fff;
  border-radius: 6.25em 6.25em 0 0;
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__logo{
    display: block;
    width: 12.4375em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: 2.5em;
    padding-top: 2.5em;
    border-top: 1px solid #939598;
  }
  &__text{
    font-size: .875em;
    line-height: 140%;
    text-align: center;
  }
}

.footer-nav{
  width: 28em;
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 2em));
  gap: 1.25em 4em;
  &__link{
    color: #fff;
    padding: .41em 0;
    font-weight: 500;
    span{
      font-size: 1.125em;
      letter-spacing: -0.0225em;
    }
    &._border{
      border-bottom: 1px solid #fff;
      display: inline-flex;
      margin-right: auto;
      &:hover{
        border-color: $primary;
      }
    }
  }
}

.footer-contacts{
  width: calc(100% / 3 - 1.25em);
  @include grid100gap(1.25em);
  &__item{
    display: flex;
    align-items: center;
  }
  &__icon{
    margin-right: 1.25em;
    width: 2.75em;
    height: 2.75em;
    flex-shrink: 0;
    background-color: $primary;
    border-radius: 50%;
    @include center;
    img{
      width: 1.5em;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__btn{
    margin-top: .31em;
    font-weight: 500;
    letter-spacing: -0.0175em;
    color: #fff;
    border-bottom: 1px dashed #fff;
    &:hover{
      color: $primary;
      border-color: $primary;
    }
  }
  .socials{
    &__item{
      path{
        fill: #fff;
      }
      &:hover{
        path{
          fill: $primary;
        }
      }
    }
  }
}



@media screen and (max-width: 992px){
  .footer{
    margin-top: -4em;
    padding: 4em 0;
    border-radius: 2.5em 2.5em 0 0;
    &__top{
      display: block;
    }
    &__logo{
      width: 9.375em;
    }
    &__bottom{
      margin-top: 2em;
      padding-top: 2em;
    }
  }

  .footer-nav{
    margin-top: 1.5em;
  }

  .footer-contacts{
    width: 100%;
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__text{
      font-size: .75em;
    }
  }

  .footer-nav{
    grid-template-columns: 1fr .7fr;
    gap: .625em 3em;
    &__link{
      span{
        font-size: 1em;
      }
    }
  }
}
