.main{
  padding: 8.125em 0;
  position: relative;
  &__content{
    position: relative;
    z-index: 2;
    max-width: 50em;
  }
  &__img{
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__desc{
    margin-top: 2.5em;
  }
  &__items{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
    max-width: 39.375em ;
  }
  &__item{
    display: flex;
    align-items: center;
    &:before{
      content: '';
      flex-shrink: 0;
      background-image: url(../img/check.svg);
      @include bgImgSize(100%);
      width: 2.125em;
      height: 2.125em;
      margin-right: .625em;
    }
    b{
      font-weight: 800;
    }
  }
  &__nav{
    display: flex;
    margin-top: 3.125em;
  }
  &__btn{
    margin-right: 1.25em;
  }
}

.calc{
  padding: 6.875em 0 3.4375em;
  &__wrap{
    margin-top: 3.125em;
    padding: 3.125em 6.25em 3.125em 5em;
    border-radius: 3.75em;
    background-color: #F5F5F5;
    display: grid;
    grid-template-columns: 5em calc(100% - 10em);
    gap: 5em;
  }
}

.calc-success{
  padding: 5em 4.375em 8.75em;
  border-radius: 3.75em;
  background-color: $primary;
  color: #fff;
  margin-top: 3.125em;
  &__text{
    max-width: 66em;
    @include grid100gap(3em);
    .h1{
      font-weight: 700;
    }
  }
  .socials{
    margin-top: 3.75em;
    &__title{
      font-size: 1.5em;
      line-height: 140%;
    }
    &__items{
      margin-left: 1.25em;
    }
    &__item{
      width: 3.125em;
      &:hover{
        rect{
          fill: darken(#fff,10%);
        }
      }
    }
  }
}

.calc-progress{
  &__item{
    color: $gray;
    position: relative;
    transition: all .3s;
    &:not(:last-child){
      margin-bottom: 3.125em;
      &:before{
        content: '';
        position: absolute;
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        width: .1875em;
        height: 1.875em;
        background-color: rgba($gray,.5);
        border-radius: 12.5em;
        margin-top: .625em;
      }
    }
    svg{
      width: 100%;
      opacity: .5;
    }
    &._filled{
      color: $primary;
      &:before{
        background-color: $primary;
      }
      ._fill{
        stroke: $primary;
      }
      svg{
        opacity: 1;
      }
    }
  }
  &__num{
    @include centerAbsolute;
  }
}

.calc-step{
  display: none;
  flex-direction: column;
  height: 100%;
  &._active{
    display: flex;
  }
  &__top{
    display: grid;
    grid-template-columns: 7.5em calc(100% - 7.5em);
    gap: .125em 0;
    margin-bottom: 1.875em;
    max-width: 45em;
  }
  &__nav{
    padding-top: 3.25em;
    margin-top: auto;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: .5em 1.25em;
    @media screen and (min-width: 577px){
      .m-btn-green{
        grid-column: 2/3;
      }
    }
  }
  &__desc{
    padding-left: 7.5em;
    margin-bottom: 2em;
  }
  &__grid{
    margin-top: 2.875em;
  }
}

.calc-cities{
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3em / 4));
    gap: 1.25em 1em;
    margin-top: 1.875em;
  }
  &__item{
    font-weight: 500;
    border-bottom: 1px solid transparent;
    transition: all .3s;
    display: inline-block;
    margin-right: auto;
    &:hover{
      cursor: pointer;
      color: $primary;
      border-color: $primary;
    }
    span{
      letter-spacing: -0.0225em;
      font-size: 1.125em;
      line-height: 144%;
    }
  }
}

.calc-certificates{
  @include grid100gap(1.25em);
}

.calc-form,
.calc-info{
  display: flex;
  flex-direction: column;
  &__btn{
    margin-top: auto;
  }
}

.calc-form{
  &__fields{
    margin-top: 1.875em;
    @include grid100gap(.875em);
  }
  .m-check{
    margin: 1.875em 0;
    label{
      padding-left: 1.875em;
    }
  }
}

.calc-info{
  &__grid{
    margin-bottom: 2em;
  }
}

.info-card{
  padding: 1.875em 1.5em 1.5em;
  border-radius: 1.25em;
  background-color: #fff;
  &__icon{
    height: 2.125em;
  }
  &__text{
    margin-top: 1em;
    @include grid100gap(1em);
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 2.25em 0 0;
    &__img{
      position: relative;
      height: auto;
      top: unset;
      img{
        height: auto;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 35%;
        transform: translateY(-50%);
        background-image: url(../img/ellipse.svg);
        @include bgImgSize(100%);
      }
    }
    &__desc{
      margin-top: 1em;
    }
    .text60{
      max-width: 70%;
    }
    &__items,
    &__nav{
      margin-top: 1.5em;
    }
  }

  .calc{
    padding: 4em 0 2em;
    &__wrap{
      margin-top: 2em;
      padding: 1.25em 1em 2em;
      border-radius: 2.5em;
      grid-template-columns: 5em calc(100% - 7em);
      gap: 2em;
    }
  }

  .calc-success{
    padding: 5em 1em 16em;
    border-radius: 2.5em;
    margin-top: 2em;
    &__text{
      gap: 2em;
      .h1{
        font-size: 2.125em;
      }
    }
    .socials{
      margin-top: 4em;
      display: block;
      &__title{
        font-size: 1.25em;
      }
      &__items{
        margin: 1.5em 0 0;
      }
    }
  }

  .calc-step{
    &__top{
      margin-bottom: 2em;
      grid-template-columns: 4.5em calc(100% - 4.5em);
    }
    &__desc{
      margin: -1.5em 0 2em;
      padding-left: 4.5em;
    }
    &__grid{
      margin-top: 0;
      grid-template-columns: 100%;
    }
  }

  .calc-cities{
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      gap: .5em;
      margin-top: 2em;
    }
    &__item{
      span{
        font-size: .875em;
        line-height: 186%;
      }
    }
  }

  .calc-form{
    &__fields{
      margin-top: 1em;
      gap: .5em;
    }
    .m-check{
      margin-top: 1em;
    }
  }



  .info-card{
    padding: 1.5em 1em;
    border-radius: 1.25em;
    background-color: #fff;
    &__text{
      margin-top: 1.5em;
    }
  }

}


@media screen and (max-width: 576px){
  .main{
    .text60{
      max-width: 100%;
    }
    &__items{
      grid-template-columns: 100%;
      gap: .625em;
    }
    &__item{
      &:before{
        margin-right: .625em;
        width: 1.5em;
        height: 1.5em;
      }
      .text24{
        font-size: 1em;
      }
      br{
        display: none;
      }
    }
    &__nav{
      display: grid;
      grid-template-columns: 100%;
      gap: .5em;
    }
    &__btn{
      margin: 0;
    }
  }

  .calc{
    &__wrap{
      overflow: hidden;
      grid-template-columns: 100%;
    }
  }

  .calc-progress{
    display: flex;
    &__item{
      width: 3.5em;
      flex-shrink: 0;
      &:not(:last-child){
        margin: 0 1.5em 0 0;
        &:before{
          top: 50%;
          left: 100%;
          transform: translateY(-50%);
          width: .625em;
          height: .1875em;
          margin: 0 0 0 .4375em;
        }
      }
    }
    &__num{
      font-size: .875em;
    }
  }

  .calc-step{
    &__top{
      grid-template-columns: 100%;
    }
    &__nav{
      grid-template-columns: 100%;
    }
    &__desc{
      padding-left: 0;
    }
  }

  .calc-cities{
    &__items{
      grid-template-columns: 1fr .6fr;
    }
  }

  .calc-info{
    &__grid{
      display: flex;
      gap: unset;
      width: calc(100% + 2em);
      padding: 0 1em;
      margin-left: -1em;
      overflow-x: auto;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
  }

  .info-card{
    flex-shrink: 0;
    width: 17em;
    @include lastM(0 .5em 0 0);
    &__text{
      gap: .75em;
    }
    &__title{
      font-size: 1em;
    }
    &__desc{
      font-size: .75em;
    }
  }
}
