.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }

    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &__bg {
        background: rgba(0, 0, 0, 0.70);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 39.0625em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 2.5em;
        border-radius: 1.25em;
        display: inline-block;
    }

    .close-btn {
        position: absolute;
        top: 1.25em;
        right: 1.25em;
        width: 1.5em;
        background-color: transparent;
        @include center;
        z-index: 3;
        svg {
            width: 100%;
            height: auto;
        }

        &:hover {
            path {
                fill: $black;
            }
        }
    }
    .text70{
        font-size: 2.5em;
    }
    &__text{
        margin-top: 2.5em;
    }
    &__cities{
        margin-top: 2.5em;
        column-count: 3;
        column-gap: 1em;
        .city-item{
            display: inline-flex;
            width: 100%;
            @include lastM(0 0 1.25em);
        }
    }
}

.city-modal{
    .modal__dialog{
        width: 52.75em;
    }
}

.feedback-modal{
    .modal__dialog{
        padding: 0;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__text{
            margin-top: 1.875em;
        }
        &__cities{
            margin-top: 1.875em;
            column-count: 2;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__cities{
            column-count: 1;
        }
    }
}
