.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.125em;
    height: 3.375em;
    font-weight: 500;
    border-radius: 3.125em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 120%;
        letter-spacing: -0.0225em;
    }
}

.m-btn-green{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
    &:disabled{
        background-color: $gray;
        pointer-events: none;
    }
}

.m-btn-green-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}
