.how-get{
  padding: 6.875em 0;
  &__top{
    max-width: 50em;
  }
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 1.875em * 5 / 6));
    gap: 1.875em;
  }
}

.how-card{
  padding: 2.75em 1.875em 2.125em;
  border-radius: 1.25rem;
  background-color: $gray2;
  grid-column: span 2;
  &._big{
    grid-column: span 3;
  }
  &__num{
    display: inline-block;
  }
  &__text{
    margin-top: 1.25em;
    @include grid100gap(1em);
  }
  &__desc{
    line-height: 140%;
    a{
      text-decoration: underline;
    }
  }
}

.reviews{
  padding: 6.875em 0;
  background-color: $gray2;
}

.reviews-slider{
  margin-top: 3.25em;
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: calc(100% / 3);
      height: auto;
      padding-right: 1.875em;
    }
    &-nav{
      display: flex;
      align-items: center;
      position: absolute;
      bottom: 100%;
      right: 0;
      margin-bottom: 3.125em;
    }
    &-button{
      margin: 0;
      position: static;
      &-next{
        margin-left: 1.25em;
      }
    }
  }
}

.swiper-button{
  flex-shrink: 0;
  width: 2.875em;
  height: 2.875em;
  border-radius: 50%;
  @include center;
  border: 2px solid #D9DBDE;
  position: relative;
  svg{
    width: 1.625em;
    height: auto;
  }
  &:hover{
    border-color: $black;
    path{
      fill: $primary;
    }
  }
}

.review-card{
  border-radius: 1.25em;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 2.5em 1.875em;
  background-color: #fff;
  &__top{
    display: flex;
    align-items: center;
  }
  &__num{
    flex-shrink: 0;
    @include center;
    width: 3.75em;
    height: 3.75em;
    border-radius: 50%;
    background-color: $primary;
    color: #fff;
    font-weight: 700;
    margin-right: .625em;
    span{
      font-size: 1.25em;
      transform: translateY(5%);
    }
  }
  &__text{
    margin: 1.25em 0;
    .text16{
      @include truncate(9);
    }
  }
  &__site{
    border-top: 1px solid #D9DBDE;
    margin-top: auto;
    padding-top: 1.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-img{
      height: 1.875em;
      display: block;
      img{
        max-height: 100%;
        max-width: 100%;
      }
    }
  }
}

.faq{
  padding: 6.875em 0;
  &__items{
    margin-top: 2.5em;
  }
}

.qa-card{
  padding: .625em 0;
  border-bottom: 1px solid #D9DBDE;
  &._active{
    .qa-card__show{
      color: $primary;
    }
    .qa-card__btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  &__show{
    cursor: pointer;
    padding: .625em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    margin-left: .5em;
    flex-shrink: 0;
    width: 2.875em;
    height: 2.875em;
    @include center;
    border: 2px solid #D9DBDE;
    position: relative;
    border-radius: 50%;
    svg{
      @include centerAbsolute;
      width: 1.625em;
      height: auto;
    }
    ._view2{
      opacity: 0;
    }
    &:hover{
      border-color: $black;
      path{
        fill: $primary;
      }
    }
  }
  &__hidden{
    padding-top: .625em;
    display: none;
  }
  &__text{
    font-size: 1em;
    line-height: 140%;
    font-weight: 300;
  }
}


@media screen and (max-width: 992px){
  .how-get{
    padding: 4em 0;
    &__grid{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
    &__top{
      max-width: 40em;
    }
  }

  .how-card{
    padding: 1.25em 1.5em;
    grid-column: unset;
    &._big{
      grid-column: unset;
    }
    &._big2{
      grid-column: 1/-1;
    }
    &__num{
      font-size: 3em;
    }
    &__text{
      margin-top: .875em;
      gap: .5em;
    }
  }

  .reviews{
    padding: 4em 0;
  }

  .reviews-slider{
    margin-top: 2em;
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        width: 50%;
        padding-right: 1em;
      }
      &-nav{
        position: static;
        justify-content: flex-end;
        margin: 1.875em 0 0;
      }
    }
  }

  .review-card{
    padding: 1.875em 1.25em;
  }

  .faq{
    padding: 4em 0;
    &__items{
      margin-top: 1.5em;
    }
  }

  .qa-card{
    &__text{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .how-get{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .reviews-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }
}
