.header{
  padding: 1.875em 0;
  position: relative;
  z-index: 10;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    width: 11.6875em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.menu{
  display: flex;
  background-color: #231F20;
  margin-top: 1.25em;
  color: #fff;
  border-radius: 3.125em;
  &__btn{
    height: 3.25em;
    flex-grow: 1;
    padding: 0 1.5em;
    border-radius: 0 3.125em 3.225em 0;
    span{
      transform: translateY(5%);
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  padding: 0 1.625em;
  &__link{
    display: flex;
    align-items: center;
    &:hover{
      color: #fff;
      span{
        border-bottom-color: #fff;
      }
    }
    &:last-child{
      &:after{
        display: none;
      }
    }
    span{
      border-top: 1px solid transparent;
      border-bottom: 1px solid transparent;
      transition: inherit;
      font-size: 1.125em;
      line-height: 144%;
      letter-spacing: -0.0225em;
      transform: translateY(5%);
    }
    &:after{
      content: '';
      width: 0.0625em;
      height: 1.5em;
      background-color: #939598;
      margin: 0 1.65em;
      flex-shrink: 0;
      pointer-events: none;
    }
  }
}

.header-contacts{
  display: flex;
  align-items: center;
  &__phone,
  &__address{
    display: flex;
    align-items: center;
    &-icon{
      width: 2.75em;
      height: 2.75em;
      background-color: $primary;
      @include center;
      margin-right: 1.25em;
      flex-shrink: 0;
      border-radius: 50%;
      img{
        width: 1.5em;
        display: block;
      }
      ._view2{
        display: none;
      }
    }
  }
  &__address,
  &__phone{
    &-btn,
    &-city{
      cursor: pointer;
      font-weight: 500;
      letter-spacing: -0.0175em;
      color: $primary;
      border-bottom: 1px dashed $primary;
    }
  }
  &__address{
    &-title{
      color: #939598;
      margin-right: .31em;
    }
  }
  &__phone{
    margin-left: 3.125em;
    &-content{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &-btn{
      margin-top: .2em;
    }
  }
  .socials{
    margin-left: 3.125em;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__items{
    display: flex;
    margin-left: .94em;
  }
  &__item{
    display: block;
    width: 2.25em;
    @include lastM(0 .625em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      rect{
        fill: darken($primary,10%);
      }
    }
  }
}

.menu-contacts,
.burger,
.menu__close,
.menu__bg{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 0;
    &__top{
      flex-wrap: wrap;
      position: relative;
      @media screen and (min-width: 577px){
        font-size: 1.5em;
        .header-contacts{
          padding: .5em 0;
          .header-contacts__phone{
            font-size: 1.5em;
            bottom: .5em;
            right: 3em;
          }
        }
      }
    }
    &__mob{
      padding: .5em 1em;
      background-color: $primary;
      width: calc(100% + 2em);
      margin-left: -1em;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__logo{
      width: 7.3125em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
        &__bg{
          display: block;
        }
      }
    }
  }

  .header-contacts{
    padding: 1.25em 0;
    width: 100%;
    order: -1;
    justify-content: space-between;
    .header-contacts__phone{
      position: absolute;
      color: #fff;
      bottom: 1.19em;
      right: 4.56em;
      &-icon{
        display: none;
      }
      &-tel{
        font-size: .75em;
      }
      &-btn{
        font-size: .5em;
        color: #fff;
        border-color: #fff;
      }
    }
    .header-contacts__address{
      &-icon{
        background-color: transparent;
        width: 1em;
        height: auto;
        margin-right: .25em;
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
        img{
          width: 100%;
        }
      }
      &-title{
        display: none;
      }
      &-city{
        font-size: .625em;
      }
    }
    .socials{
      &__title{
        display: none;
      }
      &__item{
        width: 1.5em;
        @include lastM(0 .25em 0 0);
      }
    }
  }

  .menu__close,
  .burger{
    @include center;
    width: 2.25em;
    img{
      width: 100%;
      display: block;
    }
  }

  .menu{
    position: fixed;
    top: 0;
    right: 0;
    z-index: 20;
    height: $vh;
    width: 21.5em;
    margin: 0;
    border-radius: 0;
    flex-direction: column;
    padding: 4.625em 1.5em;
    overflow-y: auto;
    overflow-x: hidden;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__btn{
      flex-shrink: 0;
      flex-grow: unset;
      border-radius: 3.125em;
      margin-top: 1.5em;
    }
    &__close{
      position: fixed;
      top: 1.5em;
      right: 1em;
    }
    &__bg{
      opacity: 0.6;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      background-color: $primary;
      z-index: 19;
    }
  }

  .nav{
    padding: 0;
    flex-direction: column;
    align-items: flex-start;
    &__link{
      @include lastM(0 0 1.5em);
      &:after{
        display: none;
      }
    }
  }

  .menu-contacts{
    display: block;
    padding-top: 4em;
    margin-top: auto;
    .header-contacts__phone{
      margin: 0 0 2em;
    }
  }
}


