.contacts{
  padding: 6.875em 0 14.75em;
  background-color: $gray2;
  &__inner{
    display: grid;
    grid-template-columns: 45.875em calc(100% - 47.75em);
    gap: 1.875em;
    margin-top: 3.125em;
  }
  &__places{
    max-height: 27.5em;
    width: calc(100% + .5em);
    padding-right: .5em;
    margin-top: 1.25em;
    overflow-y: auto;
    @include scroll;
    @include grid100gap(.3125em);
  }
}

.map{
  height: 37.5em;
  width: 100%;
}

.city-select{
  margin-top: 1.25em;
  position: relative;
  z-index: 2;
  &._open{
    .city-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    cursor: pointer;
    position: relative;
    z-index: 3;
    padding: 0 1.875em;
    display: flex;
    align-items: center;
    background-color: #fff;
    height: 5.25em;
  }
  &__icon{
    width: 2.75em;
    height: 2.75em;
    background-color: $primary;
    border-radius: 50%;
    @include center;
    margin-right: 1.25em;
    flex-shrink: 0;
    img{
      width: 1.5em;
      display: block;
    }
  }
  &__info{
    display: flex;
    align-items: center;
  }
  &__name{
    font-weight: 500;
    letter-spacing: -0.0175em;
    color: $primary;
    border-bottom: 1px dashed $primary;
  }
  &__title{
    color: #939598;
    margin-right: .31em;
  }
  &__arrow{
    margin-left: auto;
    width: 1.5em;
  }
  &__dropdown{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 5.25em 1.875em 1.875em;
    box-shadow: 0 .6875em 1.5625em 0 rgba(0, 0, 0, 0.05);
    background-color: #fff;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__variants{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 1.25em 4em;
    background-color: #F5F5F5;
    max-height: 18.5em;
    overflow-y: auto;
    @include scroll;
  }
  .city-item{
    @include lastM(0 0 1.25em);
  }
}

.city-item{
  font-weight: 500;
  span{
    font-size: 1.125em;
    line-height: 144%;
    letter-spacing: -0.0225em;
  }
  &._active{
    color: $primary;
  }
}

.address-card{
  padding: 1.875em 1.625em;
  background-color: #fff;
  @include grid100gap(1.25em);
  &__place,
  &__contacts,
  &__socials,
  &__phone{
    display: flex;
    align-items: center;
  }
  &__socials,
  &__phone{
    &:after{
      content: '';
      width: 0.0625em;
      height: 1.25em;
      flex-shrink: 0;
      margin: 0 .75em;
      background-color: #D9DBDE;
    }
    &:last-child{
      &:after{
        display: none;
      }
    }
  }
  &__place,
  &__phone{
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .75em;
    }
  }
  &__social{
    width: 1.5em;
    flex-shrink: 0;
    @include lastM(0 .5em 0 0);
    img{
      width: 100%;
      display: block;
    }
  }
}

.what{
  border-radius: 6.25em;
  background-color: $primary;
  color: #fff;
  padding: 6.875em 0;
  &__grid{
    display: grid;
    grid-template-columns: 32.25em calc(100% - 32.25em - 1.875em);
    gap: 1.875em;
    margin-top: 3.125em;
  }
  .about__items{
    gap: .625em;
  }
  .about-item{
    padding: 1.875em;
    &:before{
      display: none;
    }
  }
  &__img{
    width: 100%;
    min-height: 100%;
    display: block;
    object-fit: cover;
    border-radius: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding: 4em 0 8em;
    &__inner{
      margin-top: 2em;
      grid-template-columns: 100%;
    }
    &__map{
      width: calc(100% + 2em);
      margin-left: -1em;
    }
    &__places{
      width: calc(100% + .375em);
      padding-right: 0;
    }
  }

  .map{
    height: 22em;
  }

  .what{
    border-radius: 2.5em;
    padding: 4em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
      margin-top: 2em;
    }
    .about__items{
      gap: .5em;
    }
    .about-item{
      padding: 1.5em;
      &__title{
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .city-select{
    &__info{
      flex-direction: column;
      align-items: flex-start;
    }
    &__title{
      margin-bottom: .31em;
    }
    &__variants{
      padding-left: 1em;
      padding-right: 1em;
    }
  }

  .address-card{
    padding: 1.875em 1.25em;
    &__contacts{
      margin-top: -.5em;
      flex-wrap: wrap;
    }
    &__socials,
    &__phone{
      margin-top: .75em;
    }
    &__socials{
      &:after{
        display: none;
      }
    }
  }
}
