.fg {
  position: relative;
  margin: 0;
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    &::-ms-expand {
      display: none; // IE
    }
  }
  select,
  input {
    border: 1px solid $gray;
    padding: 0 1.875em;
    height: 3.375em;
    width: 100%;
    font-size: 1em;
    background-color: #fff;
    color: #000;
    border-radius: 31.25em;
    &::placeholder {
      color: $gray;
    }
    &:focus {
      border-color: $black;
    }
  }
  select{
    background-image: url(../img/arrow.svg);
    background-position: right 1.875em center;
    background-size: 1.5em auto;
    background-repeat: no-repeat;
    padding-right: 4.5em;
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-radio,
.m-check{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2.5em;
    padding-top: .122em;
    min-height: 1.25em;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 2px solid $gray;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      transition: 0s;
    }
    span{
      font-size: 1.125em;
      line-height: 100%;
      letter-spacing: -0.0225em;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
  &._small{
    label{
      padding-left: 1.875em;
      padding-top: 0;
    }
    span{
      font-size: .75em;
      line-height: 120%;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .75em auto;
      }
    }
  }
  label {
    &:before{
      background-image: url(../img/check2.svg);
      border-radius: .3125em;
    }
  }
}

.m-radio {
  input {
    &:checked+label {
      &::before {
        border-color: $primary;
        background-size: .5em auto;
      }
    }
  }
  label {
    &:before{
      background-image: url(../img/radio.svg);
      border-radius:  50%;
    }
  }
}
