.credit{
  padding: 3.4375em 0 6.875em;
  &__top{
    max-width: 52em;
  }
}

.credit-card{
  padding: 3.125em 1.75em 2.5em;
  border-radius: 1.25em;
  background-color: $gray2;
  &__icon{
    height: 3.125em;
    display: block;
    margin-bottom: 1.875em;
  }
  &__content{
    margin-top: 1.875em;
  }
  &__text{
    font-size: 1.25em;
    line-height: 140%;
    &:not(:first-child){
      margin-top: .5em;
    }
    button{
      display: inline-block;
      text-decoration-line: underline;
      color: $primary;
      &:hover{
        text-decoration-color: transparent;
      }
    }
  }
  &__num{
    font-size: 1.625em;
    font-weight: 700;
    line-height: 140%;
    color: $primary;
  }
}

.credit__grid,
.stat__grid{
  margin-top: 3.125em;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
  gap: 1.875em;
}

.about{
  padding: 6.875em 0;
  background-color: $primary;
  color: #fff;
  border-radius: 6.25em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 32.25em - 1.875em) 32.25em;
    gap: 1.875em;
    margin-top: 3.125em;
  }
  &__items{
    @include grid100gap(1.125em);
  }
}

.about-item{
  padding: 1.5em 3.25em 1.5em 5.25em;
  border-radius: 1.25em;
  background: #0B853A;
  @include grid100gap(.75em);
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 1.5em;
    left: 1.25em;
    width: 2.75em;
    height: 2.75em;
    background-image: url(../img/about-icon.svg);
    @include bgImgSize(100%);
  }
  &__desc{
    line-height: 140%;
  }
}

.feedback-form{
  padding: 2.5em 1.875em;
  background-color: #fff;
  border-radius: 1.25em;
  color: $black;
  position: relative;
  @include grid100gap(1.875em);
  &__fields{
    @include grid100gap(.875em);
  }
  &._success{
    .success-form{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.stat{
  margin-top: 6.875em;
  &__top{
    max-width: 64em;
  }
}

.stat-card{
  padding: 2.875em 1.875em 3.125em;
  border-radius: 1.25em;
  background: #FFF;
  color: $black;
  &__desc{
    margin-top: 1.25em;
  }
  .text30{
    line-height: 110%;
  }
}

.success-form{
  position: absolute;
  top: 0;
  left: 0;
  min-height: 100%;
  width: 100%;
  border-radius: inherit;
  background-color: #fff;
  z-index: 2;
  padding: inherit;
  opacity: 0;
  pointer-events: none;
  &__check{
    width: 6.25em;
    display: block;
  }
  &__text{
    margin-top: 2.5em;
    font-weight: 700;
    @include grid100gap(1.75em);
    .text30{
      line-height: 110%;
    }
    span{
      color: $primary;
    }
  }
  .socials{
    margin-top: 3.75em;
    justify-content: space-between;
    &__title{
      font-size: 1.125em;
      line-height: 110%;
      max-width: 15em;
    }
    &__item{
      width: 3.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .credit{
    padding: 2em 0 4em;
    &__top{
      max-width: 33em;
    }
    &__grid{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
  }

  .credit-card{
    padding: 2em 1.5em;
    &__text{
      font-size: 1em;
    }
    &__num{
      font-size: 1.25em;
    }
  }

  .about{
    padding: 4em 0;
    border-radius: 2.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
      margin-top: 2em;
    }
    &__items{
      gap: .5em;
    }
    .feedback-form{
      padding: 1.5em;
    }
  }

  .about-item{
    padding: 1.5em 1em 1.5em 5em;
    gap: .5em;
    &:before{
      top: 1.5em;
      left: 1em;
    }
    &__title{
      font-size: 1.25em;
      line-height: 110%;
    }
  }

  .stat{
    margin-top: 4em;
    &__grid{
      margin-top: 2em;
      grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      gap: .5em;
    }
  }

  .stat-card{
    padding: 1.5em;
    &__title{
      font-size: 3em;
    }
    &__desc{
      margin-top: .25em;
    }
    .text30{
      font-size: 1.125em;
    }
  }

  .success-form{
    &__text{
      margin-top: 1.75em;
      gap: 1.5em;
      span{
        display: block;
      }
    }
    .socials{
      &__title{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .credit{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .stat{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .success-form{
    .socials{
      &__title{
        max-width: 8em;
      }
    }
  }
}
